}

img {
  border: none;
  vertical-align: bottom;
}

a {
  cursor: pointer;
  text-decoration: none;
}

input,
textarea {
  -webkit-appearance: none;
  border-radius: 0;
}

html {
  font-size: 16px;
}

body {
  color: #000000;
  font-family: "游明朝", "Yu Mincho", YuMincho, serif;
